@import 'grid';
@import 'fonts';
@import 'vars';

body {
	font-size: 16px;
	line-height: 1.6;
	font-family: $primary-font;
	min-width: 320px;
	overflow-x: hidden;
	position: relative;
	color: $color;
	background-color: #fafafa;
}

.box {
	margin: 30px 0;
	display: block;
	padding: 30px;
	background-color: #fff;
	@include box-shadow;
}

*{
	outline: none !important;
}

.background-image {
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}

h1, h2, h3, h4, h5, h6 {
	font-family: $heading-font;
	letter-spacing: 1px;
}

a {
	@include transition;
	color: $gray;
	&:hover {
		color: $accent;
		text-decoration: none;
	}
}

.text-center {
	text-align: center;
}