#header {
	min-height: 360px;
	position: relative;
	overflow: hidden;
	box-shadow: inset 0 0 50px rgba(#000, .5);
}

.header-bg {
	filter: blur(7px);
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	z-index: -1;
	&::after {
		content: '';
		display: block;
		background-color: #fff;
		opacity: 0.5;
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
	}
}

.top-panel {
	height: 80px;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	padding-top: 0;
	padding-bottom: 0;
}

.site-branding {
	flex-grow: 2;
	height: 100%;
	margin-left: -30px;
	a {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 30px;
		float: left;
		height: 100%;
		text-decoration: none !important;
		color: $gray;
	}
	.site-description {
		margin: 0;
		font-size: em(14px);
	}
}

.site-navigation {
	margin-right: -30px;
	height: 100%;
	#toggle-nav {
		height: 100%;
		padding: 0 30px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		text-decoration: none !important;
		color: $gray-light;
		font-size: 24px;
		&:hover {
			color: $accent;
		}
	}
}

.site-carousel {
	.post-item {
		background-color: #fff;
		height: 220px;
		@include box-shadow;
		position: relative;
		&::before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(#000, 0.1);
			z-index: 0;
			@include transition($animation-timing);
		}
		&:hover {
			&::before {
				opacity: 0;
			}
		}
		.post-link {
			display: flex;
			flex-direction: column;
			justify-content: center;
			position: relative;
			z-index: 1;
			color: $color;
			text-decoration: none;
			text-align: center;
			height: 100%;
			overflow: hidden;
			padding: 40px;
			&::before, &::after {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(#fff, 0.65);
				@include transition($animation-timing);
			}
			&::before {
				transform: translate(100%, -100%);
			}
			&::after {
				transform: translate(-100%, 100%);
			}
			&:hover {
				&::before, &::after {
					transform: translate(0);
				}
				.content {
					transform: rotateX(0) rotateY(0);
				}
			}
		}
		.content {
			position: relative;
			z-index: 1;
			transform: rotateX(90deg) rotateY(-90deg);
			@include transition($animation-timing);
		}
		.post-title {
			margin: 0;
			line-height: 1em;
			font-size: em(24px);
			&::after {
				content: '';
				display: block;
				border-top: 1px solid $color;
				width: 50%;
				margin: 15px auto 10px;
			}
		}
		.short-description {
			margin: 0;
			font-size: em(14px);
		}
	}
}