.post {
	display: flex;
	padding: 0;
	@media(max-width: 992px) {
		flex-direction: column;
	}
	&:nth-child(even) {
		flex-direction: row-reverse;
		@media(max-width: 992px) {
			flex-direction: column;
		}
	}
	&:hover {
		.picture::before {
			opacity: 0;
		}
	}
	.picture {
		height: 360px;
		width: 50%;
		position: relative;
		overflow: hidden;
		@media(max-width: 992px) {
			width: 100%;
		}
		@media(max-width: 768px) {
			height: 300px;
		}
		@media(max-width: 480px) {
			height: 260px;
		}
		&::before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: rgba(#000, 0.1);
			@include transition;
		}
		.post-link {
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
		}
	}
	.post-content {
		width: 50%;
		padding: 30px;
		display: flex;
		flex-flow: column;
		justify-content: center;
		@media(max-width: 992px) {
			width: 100%;
		}
		.post-title {
			font-size: em(24px);
			margin-top: 0;
			a {
				color: $color;
			}
		}
		.post-info {
			color: $gray;
			font-size: em(14px);
			a {
				&:hover {
					text-decoration: underline;
				}
			}
		}
		.content {
			font-size: em(16px);
		}
		.read-more {
			display: inline-block;
			margin: 0 auto;
			text-transform: uppercase;
			color: $gray;
			font-weight: 300;
			&:hover {
				text-decoration: underline;
				color: $accent;
			}
		}
	}
}

.pagination {
	margin: 30px auto;
	text-align: center;
	font-size: 14px;
	color: $gray;
	cursor: default;
	display: inline-block;
	width: 100%;
	.page-number {
		@include box-shadow;
		background-color: #fff;
		padding: 7px 20px;
		border-radius: 10em;
		margin: 0 10px;
	}
	a {
		&:hover {
			text-decoration: underline;
		}
	}
}