@mixin resp($media, $size, $size2: '') {
	@if $media == max {
		@media only screen and (max-width: $size) { @content; }
	}
	@else if $media == minmax {
		@media only screen and (min-width: $size + 1) and (max-width: $size2 - 1) { @content; }
	}
	@else if $media == min {
		@media only screen and (min-width: $size) { @content; }
	}
}

@mixin box-shadow() {
	box-shadow: 3px 3px 7px rgba(#000, 0.1);
}

@mixin transition($time:320ms) {
	transition: all $time ease;
}